import React, {Fragment, useMemo} from 'react';

import {Container, Divider, Grid, ListItemText, Typography} from '@mui/material';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import {Link, PageProps, graphql} from 'gatsby';

import {BranchProps} from './branches';
import CollegeSelector from '../components/CollegeSelector';
import Layout from '../components/layout';
import {createSlug} from '../utils/CommonUtils';
const locations = ['Ariyalur', 'Chengalpattu', 'Chennai', 'Coimbatore', 'Cuddalore', 'Dharmapuri', 'Dindigul', 'Erode', 'Kallakurichi', 'Kancheepuram', 'Kanyakumari', 'Karur', 'Krishnagiri', 'Madurai', 'Mayiladuthurai', 'Nagapattinam', 'Namakkal', 'Perambalur', 'Pudukkottai', 'Ramanathapuram', 'Ranipet', 'Salem', 'Sivaganga', 'Tenkasi', 'Thanjavur', 'The Nilgiris', 'Theni', 'Thirupattur', 'Thoothukudi', 'Tiruchirappalli', 'Tirunelveli', 'Tiruppur', 'Thiruvallur', 'Thiruvannamalai', 'Thiruvarur', 'Vellore', 'Viluppuram', 'Virudhunagar'];

type DataProps = {
  allStrapiBranche: {
    nodes: BranchProps[];
  };
};

const IndexPage = ({data: {allStrapiBranche}}: PageProps<DataProps>) => {
  const currentYear = useMemo(() => new Date().getFullYear(), []);
  return (
    <Layout>
      <Container sx={{my: 4}}>
        <Grid container
          direction="row"
          justifyContent="center"
          alignItems="center">
          <Typography variant="h6" component="div" color="secondary">
            TamilNadu Eng. Colleges - {currentYear}
          </Typography>
        </Grid>
        <CollegeSelector branches={allStrapiBranche.nodes} />
        <BestCollegeBanner title="பி.இ./பி.டெக். பட்டப்படிப்புகள் தன்னாட்சி அல்லாத அண்ணா பல்கலைக்கழக கல்லூரிகளின் கல்வித் திறனாய்வு"
          description='ஏப்ரல் / மே 2022 தேர்வுகளின் படி'
          link='/engineering-colleges-pass-percentage-in-2022'/>
        <BestCollegeBanner title="தமிழகத்தின் டாப் இன்ஜினீயரிங் கல்லூரிகள் - ஆனந்த விகடன் மெகா சர்வே முடிவுகள்"
          description='- நன்றி விகடன்'
          link='/top-colleges-in-tamilnadu-vikatan-survey'/>
        <BestCollegeBanner title="Best Colleges in Chennai"
          description='Colleges based on Facilities, Placements, and Best Infrastructure.'
          link='/top-colleges/chennai'/>
        <BestCollegeBanner title="Best Colleges in Coimbatore"
          description='Colleges based on Facilities, Placements, and Best Infrastructure.'
          link='/top-colleges/coimbatore'/>
        <BestCollegeBanner title="Best Colleges in Tiruchirappalli"
          description='Colleges based on Facilities, Placements, and Best Infrastructure.'
          link='/top-colleges/tiruchirappalli'/>
        <BestCollegeBanner title="Best Colleges in Madurai"
          description='Colleges based on Facilities, Placements, and Best Infrastructure.'
          link='/top-colleges/madurai'/>
        <BestCollegeBanner title="Best Colleges in Tamilnadu"
          description='Colleges based on Facilities, Placements, and Best Infrastructure.'
          link='/top-colleges/all-colleges'/>
        
        <Typography>
        {"Join our community-driven effort to simplify college selection and admission by providing comprehensive information. Your valuable input will help us promote transparency in education. Fill out the form below to contribute: "}
        <a href='https://forms.gle/AdiirR3cY4xAErsZ6' target='_blank'>https://forms.gle/AdiirR3cY4xAErsZ6</a>
        </Typography>
      </Container>

      <Container sx={{mt: 2}}>
        <Typography variant="h6" component="div">
            All Locations
        </Typography>
      </Container>
      <List>
        {locations.map((location) => {
          const branchSlug = createSlug(location);
          return (
            <Fragment key={location}>
              <ListItem disablePadding>
                <ListItemButton component={Link} to={`/top-colleges/${branchSlug}`}>
                  <ListItemText primary={location} />
                </ListItemButton>
              </ListItem>
              <Divider />
            </Fragment>
          );
        })}
      </List>
      <Container sx={{height: 100}} />
      {/* <Button component={Link} to="/branches" variant="contained" color="primary">
    Branches
</Button>
    <Button component={Link} to="/colleges" variant="contained" color="primary">
    Colleges
</Button> */}
    </Layout>
  );
};

export default IndexPage;


export const query = graphql`
  query {
    allStrapiBranche(sort: {name: ASC}) {
      nodes {
        name
        code
        id
      }
    }
  }
`;


const BestCollegeBanner = ({
  title,
  description,
  link
}:{
  title: string,
  description: string,
  link: string
}) => (
  <Paper elevation={0}
    sx={{
      backgroundColor : '#f5f5f5',
      my              : 2,
      p               : 2,
    }}>
    <Typography variant="h6" component="div"
      gutterBottom>
      {title}
    </Typography>
    <Typography variant="body1" gutterBottom>
      {description}
    </Typography>
    <Stack direction="row"
      justifyContent="flex-end"
      alignItems="center"
      spacing={2}>
      <Button size="small" component={Link} to={link}>
              View Colleges
      </Button>
    </Stack>
  </Paper>
);