import React, {FC, Fragment, useCallback, useEffect, useState} from 'react';

import {Container, Stack} from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select, {SelectChangeEvent} from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import {Link} from 'gatsby';
import {sortBy} from 'lodash';

import {LOCATION_NAMES, COMMUNITIES_LIST} from '../constants/constants';
import {BranchProps} from '../pages/branches';
import {StorageKeys} from '../types/common';
import {getCommunityNameFromLocalStorage, getSelectedBranchesFromLocalStorage, getSelectedLocationIdFromLocalStorage} from '../utils/CommonUtils';
import {LocalStorageUtil} from '../utils/LocalStorageUtil';

// const communityList = sortBy(COMMUNITIES_LIST);

const MAX_LIMIT_OPTIONS = 3;

type Props = {
  branches: BranchProps[]
}
const CollegeSelector: FC<Props> = ({branches}) => {


  const [selectedCommunity, setSelectedCommunity] = useState('');
  const [selectedLocation, setSelectedLocation] = useState('');
  const [selectedBranches, setSelectedBranches] = useState<BranchProps[] | []>([]);

  const handleOptionSelect = useCallback((event: any, value: any) => {
    if (value.length <= MAX_LIMIT_OPTIONS) {
      LocalStorageUtil.setItem(StorageKeys.Branch, JSON.stringify(value));
      setSelectedBranches(value);
    }
  }, []);
  // console.log(branches);
  useEffect(() => {
    const communityName = getCommunityNameFromLocalStorage();
    if (communityName) {
      setSelectedCommunity(communityName);
    }
    const selectedLocation = getSelectedLocationIdFromLocalStorage();
    if (selectedLocation) {
      setSelectedLocation(selectedLocation);
    }
  }, []);
  useEffect(() => {
    const selectedBranches = getSelectedBranchesFromLocalStorage(branches);
    if (selectedBranches) {
      setSelectedBranches(selectedBranches);
    }
    
  }, [branches]);

  const handleCommunityChange = useCallback((event: SelectChangeEvent) => {
    const {value} = event.target;// as unknwon as string;
    LocalStorageUtil.setItem(StorageKeys.Community, value);
    setSelectedCommunity(value);
  }, []);
  const handleLocationChange = useCallback((event: any) => {
    const {value} = event.target;
    const location = LOCATION_NAMES.filter(location => location.id === value)?.[0];
    LocalStorageUtil.setItem(StorageKeys.Location, JSON.stringify(location));
    setSelectedLocation(value.toString());
  }, []);

  //   const onDialogOpen = useCallback(() => {
  //     setDialogOpen(true);
  //   }, []);

  const renderInput = useCallback((params: any) => (
    <TextField {...params} label="Branches" placeholder="Branches"
      helperText={`Select upto ${MAX_LIMIT_OPTIONS} branches`} />
  ), []);

  return (
    <>
      <Container sx={{my: 2}}>
        {/* <FormControl sx={{my: 1}}>
          <RadioGroup defaultValue="rank"
            name="radio-buttons-group"
            row>
            <FormControlLabel value="rank" control={<Radio />} label="Rank" />
            <FormControlLabel value="cutoff" control={<Radio />} label="Cutoff" />
          </RadioGroup>
        </FormControl> */}
       

        <FormControl sx={{my: 1}} fullWidth>
          <Autocomplete multiple options={branches} limitTags={MAX_LIMIT_OPTIONS}
            value={selectedBranches}
            onChange={handleOptionSelect}
            getOptionLabel={function(option) {
              return option.name;
            }}
            renderInput={renderInput} />
        </FormControl>

        <FormControl sx={{my: 1}} fullWidth>
          <InputLabel>Location</InputLabel>
          <Select value={selectedLocation}
            label="Location"
            onChange={handleLocationChange}>
            {LOCATION_NAMES.map(location => (
              <MenuItem key={location.id} value={location.id}>{location.name}</MenuItem>
            ))}
          </Select>
        </FormControl>
        {/* <Button variant="contained"
          onClick={onDialogOpen}>Locations</Button> */}
        <Stack direction="row"
          justifyContent="center"
          alignItems="center"
          sx={{my: 3}}>
          <Button variant="contained"
            component={Link} to={'/search-results'}
            disabled={!selectedLocation}>Search Colleges</Button>
        </Stack>

        {/* <FormControl sx={{my: 1}} fullWidth>
        <TextField id="outlined-basic" label="Enter your General Rank" variant="outlined" />
        </FormControl>
        <FormControl sx={{my: 1}} fullWidth>
          <InputLabel>Community</InputLabel>
          <Select value={selectedCommunity}
            label="Community"
            onChange={handleCommunityChange}>
            {COMMUNITIES_LIST.map(community => (
              <MenuItem key={community} value={community}>{community}</MenuItem>
            ))}
          </Select>
        </FormControl> */}
        {/* <Stack direction="row"
          justifyContent="center"
          alignItems="center"
          sx={{my: 3}}>
          <Button variant="contained"
            component={Link} to={'/search-results'}
            disabled={!selectedLocation}>Search by 2022 Rank</Button>
        </Stack> */}

        {/* <Dialog onClose={onDialogClose} open={isDialogOpen}
          fullScreen>
          <DialogTitle>Set backup account</DialogTitle>

          <DialogContent>
            <List sx={{pt: 0}}>
              {LOCATION_NAMES.map((location) => (
                <ListItem key={location.name} disableGutters>
                  <ListItemButton>
                    <ListItemText primary={location.name} />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          </DialogContent>

          <DialogActions>
            <Button onClick={onDialogClose}>Cancel</Button>
            <Button onClick={onDialogClose} autoFocus>
            Apply
            </Button>
          </DialogActions>
        </Dialog> */}

      </Container>
    </>
  );
};

export default CollegeSelector;
